<template>
  <div class="staticReports">
    <el-container>
      <el-header>
        <div class="staticReports loadingtext minWidth"
             v-loading="imgLoading"
             element-loading-text="加载中..."
             element-loading-background="rgba(255, 255, 255, 0.8)">
          <div class="taticReports-content">
            <el-row type="flex" class="staticReports-header" justify="space-between">
              <el-col :span="17" class="titleMT">
                <span style="float: left;" class="mz" @click="$router.back(-1)">流程报表</span>
                <i :class="icon"></i>
              </el-col>
              <el-col :span="6" class="titleBtn clearfix" style="padding-right: 15px;">
                <el-button round @click="reset" type="primary"   size="mini">重置</el-button>
                <el-button round @click="search" type="primary"  size="mini">查询</el-button>
              </el-col>
            </el-row>
            <br>
            <!--<hr>-->
            <div class="statement reportCondition">
              <span class="fz16">动态-耗时最长的流程名</span>
              <el-row class="staticReports-middle lh35 staticReportsMiddle">
                <el-col :span="2" :sm="4" :md="1" class="classSelect"><span>流程分类选择</span></el-col>
                <el-col :span="7" class="classSelectMR process-inputbox">
                  <i class="iconfont icon-organization"  @click="openDailog('processClassification')"></i>
                  <span @dblclick="openDailog('processClassification')">
                  <el-input  disabled v-model="processCategoryName"    class="input-text"  :title="processCategoryName"></el-input>
                  </span>
                </el-col>
                <el-col  :span="1"><span>&nbsp;</span></el-col>
                <el-col :span="2" :sm="4" :md="1" class="processSelect"><span>流程选择</span></el-col>
                <el-col :span="6" class="process-inputbox">
                  <i class="iconfont icon-organization"  @click="openDailog('processSelection')"></i>
                  <span @dblclick="openDailog('processSelection')">
                  <el-input  disabled v-model="processName"    class="input-text"  :title="processName"></el-input>
                  </span>
                </el-col>
                <!--<el-col :span="1" class="textIndent"><i class="iconfont icon-organization"  @click="openDailog('processSelection')"></i></el-col>-->
              </el-row>

              <el-row class="reportCondition-date reportConditionDate">
                <el-col :span="2" :sm="4" :md="1" class="timeDuan"><span>时间段</span></el-col>
                <el-col :span="8" :sm="8" class="timeSelect">
                  <el-radio-group v-model="selectValue" @change="change">
                    <el-radio :label="1">本周</el-radio>
                    <el-radio :label="2">本月</el-radio>
                    <el-radio :label="3">本季度</el-radio>
                    <el-radio :label="4">本年</el-radio>
                    <el-radio :label="5">其他</el-radio>
                  </el-radio-group>
                </el-col>
                <el-col :span="2" :sm="4" :md="1" class="space"><span>&nbsp;</span></el-col>
                <el-col :span="7" :sm="6" v-if="selectValue === 5 ">
                  <div class="border" >
                    <el-date-picker
                      style="width: auto"
                      v-model="model.date"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator="~"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期">
                    </el-date-picker>
                  </div>

                </el-col>
              </el-row>
              <el-row>
                <el-col :span="4"  class="buttonWrap buttonWrapTA" >
                  <el-button class="exportBtn" round @click="exportList" type="primary" icon="iconfont icon-export" size="mini">导出
                  </el-button>
                </el-col>
              </el-row>
            </div>
            <div class="statement" >
              <el-table
                ref="singleTable"
                :data="tableData"
                highlight-current-row
                :height="isShow?fullHeight-320:fullHeight-215"
                border
                @sort-change="sortChange"
                class="tableWidth">
                <el-table-column
                  prop="processID"
                  label="流程ID"
                  width="150px"
                  sortable='custom'
                  align="center">
                </el-table-column>

                <el-table-column
                  prop="processName"
                  align="left"
                  sortable='custom'
                  label="流程名称">
                  <template slot-scope="scope">
                    <div class="table-popover" :title="scope.row.processName">{{ scope.row.processName }}</div>
                  </template>
                </el-table-column>

                <el-table-column
                  prop="processFullName"
                  align="left"
                  sortable='custom'
                  label="流程路径">
                </el-table-column>

                <el-table-column
                  prop="minuteSum"
                  width="150px"
                  sortable='custom'
                  label="流程平均总耗时">
                  <template slot-scope="scope">
                    <div >{{ scope.row.days }}</div>
                  </template>
                </el-table-column>
                <div slot="empty" v-show="!imgLoading">
                  <p :style="{'marginTop': '23px'}">暂无数据</p>
                </div>
                <div slot="append">
                  <!--分页-->
                  <page v-if="tableData.length > 0" :pageIndex="pageIndex"
                        :total="total"
                        :pageSize="pageSize"
                        :pageSizesList="pageSizesList"
                        @handleSizeChange="handleSizeChange"
                        @handleCurrentChange="handleCurrentChange">
                  </page>
                </div>
              </el-table>
            </div>

          </div>
        </div>
        <!--弹窗组件-->
        <component :is="componentParam.componentType" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                   v-bind:componentParam="componentParam"></component>
      </el-header>
    </el-container>

  </div>
</template>

<script>

  import processClassification from '../components/dailog/processClassification.vue'
  import processSelection from '../components/dailog/processSelection.vue'

  import util from '../js/util'
  import moment from 'moment'
  import loading from '../plugins/loading/loading'
  import page from "../plugins/page/index";

  /*本周*/
  var firstDay = moment().day(0).format('YYYY-MM-DD');
  var lastDay = moment().day(6).format('YYYY-MM-DD');

  /*本月*/
  var firstMonth = moment().startOf('month').format('YYYY-MM-DD');
  var lastMonth = moment().endOf('month').format('YYYY-MM-DD');

  /*本季度*/

  var firstSeason = moment().startOf('quarter').format('YYYY-MM-DD');
  var lastSeason = moment().endOf('quarter').format('YYYY-MM-DD');

  /*本年*/

  var firstYear = moment().startOf('year').format('YYYY-MM-DD');
  var lastYear = moment().endOf('year').format('YYYY-MM-DD');


  export default {
    name: 'peopleJob',
    //在html模板渲染后调用这个方法展示列表数据
    mounted() {
      // this.getResultList();

    },
    //调用外部组件
    components: {
      'page':page,
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection             //选择流程
    },
    data() {
      return {
        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processCategoryList:[],//流程分类选中数据
        processName:'',// 流程名
        processIDs:'',//流程id
        processList:[],//流程选中数据

        model: {
          selectForm: {},
          date:''  //选择时间
        },
        OrgId:'',
        pageIndex: 1,//当前分页的索引
        total: 0,   //数据总条数
        pageSize: 10, //当前分页显示的条数
        pageSizesList: [10,20, 50, 100], //分页按钮
        fullHeight: document.documentElement.clientHeight, //窗口可见区域高度
        selectValue: 1,
        startDate: '',//开始日期
        endDate: '',//结束日期
        tableData: [], //列表数据

        showdialog: false, //流程选择弹窗是否显示
        //component 弹窗 子组件 所有参数
        componentParam:{
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        isShow: true,    // 报表条件标题内容是否展开收起
        arrt: '收起',    // 报表条件提示字
        icon: 'el-icon-arrow-down', // 报表条件切换箭头
        orderBy:'',
        // 加载动画
        imgLoading: false
      }
    },

    methods: {
      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processCategoryList= data.list;
          let  processCategoryIDs = [];
          let  processCategoryName = [];
          for(let node of data.list){
            processCategoryName.push(node.name);
            processCategoryIDs.push(node.id+'');
          }
          this.processCategoryIDs = processCategoryIDs.join(',');
          this.processCategoryName = processCategoryName.join('；');
        }else if(data.type && data.type == "processSelection"){
          //流程 选择
          this.processList = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.processIDs = processId.join(',');
          this.processName = processName.join('；');
        }
        this.hideComponent();
      },
      //表格排序
      sortChange(column){
        this.orderBy = `${(column.prop=='statusName')?'status':column.prop} ${column.order.replace('ending','')}`;
        this.getResultList();
      },

      /**
       * 弹窗组件带出数据赋值给主组件
       */

      showToggle() {
        this.isShow = !this.isShow;
        if (this.isShow) {
          this.arrt = "收起";
          this.icon = "el-icon-arrow-up"
        } else {
          this.arrt = "展开";
          this.icon = "el-icon-arrow-down"
        }
      },

      // 搜索
      search() {
        this.getResultList();
      },

      change(){
        switch (this.selectValue) {
          case 1:
            this.startDate = firstDay;
            this.endDate = lastDay;
            break;
          case 2:
            this.startDate = firstMonth;
            this.endDate = lastMonth;
            break;
          case 3:
            this.startDate = firstSeason;
            this.endDate = lastSeason;
            break;
          case 4:
            this.startDate = firstYear;
            this.endDate = lastYear;
            break;
          case 5:
            this.startDate = this.model.date[0] ?`${this.model.date[0]} 00:00:00`:'';
            this.endDate = this.model.date[1] ?`${this.model.date[1]} 23:59:59`:'';
            break;
        }
      },
      //获取报表结果请求数据
      getResultList(){
        if (this.processIDs.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning('请选择流程分类或流程选择进行查询！');
          return;
        }
        this.change();
        var data={
          startDate:this.startDate,
          endDate:this.endDate,
          processIDs:this.processIDs,
          processCategoryIDs:this.processCategoryIDs,
          pageSize:this.pageSize,
          pageIndex:this.pageIndex-1,
          orderBy:this.orderBy

        }
        if(this.selectValue==5 && this.startDate=='' && this.endDate=='') {
          this.$message.warning('请选择日期');
          return
        }
        // loading.startLoading();
        this.imgLoading = true;
        this.$axios.post('api/reportForm/getMaximumDynamicFlowTime',data).then( (res) => {
          if(res.status===200){
            this.tableData = res.data.processList;
            this.total = res.data.listCounts;
          }else {
            this.$message.error("请求失败！");

          }
          // loading.endLoading()
          this.imgLoading = false;
        }).catch(err => {
          // loading.endLoading()
          this.imgLoading = false;
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })
      },
      /**
       * 导出
       */
      exportList() {

        this.change();
        loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        let that = this;
        let obj = {
          "tableName":"动态-流程-时效最长的流程名",
          params: {
            "startDate": that.startDate,
            "endDate": that.endDate,
            processCategoryIDs:this.processCategoryIDs,
            "processIDs": that.processIDs,
            "pageIndex":0,
            "pageSize": 1000,
            "orderBy":that.orderBy,
            "filterEmptyComment":"0",
            "userID":"",
            "selectValue": that.selectValue
          }
        };
        that.$axios.post('/api/exportExcelForTable', obj, {responseType: 'blob'}).then((result) => {
          const content = result.data;
          const dowLoadFileName = '动态-流程-时效最长的流程名';
          util.exportSearchList(dowLoadFileName, content);
          loading.endLoading();
        }).catch((err) => {
          loading.endLoading();
          that.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
        });
      },
      // 重置
      reset() {
        this.componentParam.backfillData = new Array();
        this.processList = new Array();
        this.processCategoryList = new Array();
        this.$nextTick(() => {
          this.selectValue = 1;
          this.model.date = '';//日期
          this.processName = '';//流程类别
          this.processIDs='';//流程ID
          this.processCategoryName = '';//流程分类名
          this.processCategoryIDs='';//流程分类id
        })
      },
      //翻页
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        this.getResultList();
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getResultList();
      },
    }
  }
</script>
<style lang="scss" scoped>
  .titleMT:hover {
    cursor: pointer;
    color: #1fa0fd;
  }
</style>

<style lang="scss">
  .exportBtn {
  i.icon-export {
    color: #1fa0fd !important;
  }
  /*i.icon-export:hover {*/
  /*color: #ffffff !important;*/
  /*}*/
  }
  .exportBtn:hover {
    background: #1fa0fd !important;
    color: #ffffff !important;
    i.icon-export {
      color: #ffffff !important;
    }
  }
</style>



